import { Component, OnInit, ViewChild, ElementRef, HostListener } from '@angular/core';
import { JobsService, CompaniesService } from 'src/app/services';
import { ActivatedRoute, Params } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Job } from 'src/app/models';
import { ProfileService } from 'src/app/providers/profile/profile.service';
import { NotifierService } from 'angular-notifier';
@Component({
  selector: 'app-job-applicants',
  templateUrl: './job-applicants.component.html',
  styleUrls: ['../../../../assets/css/pages/search.css']
})
export class JobApplicantsComponent implements OnInit {
  current_user
  companyslug
  job_slug;
  defaultImageBath;
  company
  job:Job={
    title:"",
    description:"",
    have_daily_perks: false,
    daily_perks_budget:null,
    have_transportation: false,
    transportation_budget:null,
    have_meal:false,
    meal_budget:null,
    have_space_rest: false,
    space_rest_budget: null,
    is_male: false,
    is_female: false,
    age:null,
    hide_company: false,
    latitude: null,
    longitude: null,
    category:[],
    publish_date:"",
    job_category:[]
  }
  originalJob;
  companyName;
  is_company;
  lookUps
  users;
  Next=null
  once=false;
  @ViewChild('infocard') infocard:ElementRef
  behavior: "auto" | "smooth";
  selectedUser:any;
  showTrue=false
  pickedUp=[]

  @HostListener('window:scroll', ['$event'])
  scroll() {
    if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight) {
      // you're at the bottom of the page
      if(this.Next&&!this.once){
        this.once=true;
          this.jobService.getNext(this.Next).subscribe(
            (res:any)=>{
              this.Next=res.next
              this.once=false
              this.users=[...this.users,...res.results];
            }
          )
        }
    }

    }

  constructor(
    private jobService:JobsService,
    private route: ActivatedRoute,
    private companiesService:CompaniesService,
    private spinner:NgxSpinnerService,
    private profile: ProfileService,
    private notifier: NotifierService,
  ) { }
  ngOnInit() {
    this.current_user = JSON.parse(localStorage.getItem('user'));
    this.route.params.subscribe(
      (params:Params)=>{
          this.job_slug=params.slug;

          this.companyslug=params.compSlug;
          this.spinner.show();
          this.companiesService.getCompany(this.current_user.slug,this.companyslug).subscribe(
            res=>{
              this.company=res;

              this.companyName=res.name;
              if(localStorage.getItem('switch'))
                {
                  this.is_company=true;
                  //this.getJobForAdmin();
                  this.getAppliedForJob();

                }

            }
          )
      }
    )
  }
getAppliedForJob(){
  this.jobService.getAppliedForJob(this.companyslug,this.job_slug).subscribe(
    (res:any)=>{
      this.users=res.results;

      this.Next=res.next;
      // this.users=[...this.users,...this.users,...this.users]
      for(let i=0;i<this.users.length;i++){
        this.users[i]['selected']=false;
      }
      this.spinner.hide();
    },err=>{
      this.spinner.hide();
    }
  );
}

showInfo(user){

  this.selectedUser=user;
  this.selectedUser['selected']=user['selected'];
  for(let i=0;i<this.users.length;i++){
    if(this.users[i].id!==user.id)
      this.users[i]['selected']=false;
  }

}
unselected(user){
  return false
}
navigateToInfoCard(){
  if(this.infocard)
    this.doScroll( this.infocard );
}
doScroll( elementRef: ElementRef ) : void {
  elementRef.nativeElement.scrollIntoView({
      behavior: this.behavior,
      block: "start"
  });
}
addToPickedUp(user, index){
  let flag=this.pickedUp.filter(function(ele){
    return ele.id == user.id;});
    if(!flag.length){
      this.pickedUp.push(user);
    }
    else{
      this.removeFromPickedUp(user.id);
    }
}
removeFromPickedUp(id) {

  this.pickedUp=this.pickedUp.filter(function(ele){
      return ele.id != id;
  });
}
pickUpApplicants(){
  for(let i=0;i<this.pickedUp.length;i++){
    this.postApplicant(this.pickedUp[i]);
  }

  this.getAppliedForJob();
}
postApplicant(user){
  this.spinner.show();
  this.jobService.postApplicant(this.companyslug,this.job_slug,user.user_profile.slug,user).subscribe(
    (res:any)=>{
      this.notifier.notify('success','Applicant was added to the picked up list');
      this.spinner.hide();
      this.getAppliedForJob();
    },
    err=>{
      this.notifier.notify('error','Some thing wrong occured');
      this.spinner.hide();
    }
  )
}
checkIndex(i){
  if((i+4)%4==0&&i==0)
  return '0px';
  else if((i+4)%4==0&&this.users.length%5==0)
  return '-240px';
  else if((i+4)%4==1&&this.users.length%6==0)
  return '-480px';
  else if((i+4)%4==1)
  return '-240px';
  else if((i+4)%4==2)
  return '-480px';
  else if((i+4)%4==3)
  return '-720px';
}
}
