import { Component, OnInit, HostListener } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Company } from 'src/app/models';
import { CompaniesService, JobsService, GoogleMapsApiService } from 'src/app/services';
import { Options } from 'ng5-slider';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-job-dashboard',
  templateUrl: './job-dashboard.component.html',
  styleUrls: ['../../../../assets/css/pages/search.css']
})
export class JobDashboardComponent implements OnInit {
  company:Company={
    about: "",
    avatar: null,
    cover: null,
    headquarter: "",
    id: null,
    is_address_public: true,
    name: "",
    since: "",
    size_from: "",
    size_to: "",
    slug:"",
    website: "",
    tags:[],
    is_admin:false,
  };
  current_user;
  companySlug;
  jobs
  jobSlug
  pickedUps
  Next=null
  once=false;
  applicants=false
  pickedup=false
  interview=false
  @HostListener('window:scroll', ['$event'])
  scroll() {
    if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight) {
      // you're at the bottom of the page
      if(this.Next&&!this.once){
        this.once=true;
          this.jobService.getNext(this.Next).subscribe(
            (res:any)=>{
              this.Next=res.next
              this.once=false
              this.pickedUps=[...this.pickedUps,...res.results];
            }
          )
        }
    }

    }
  constructor(
    private route:ActivatedRoute,
    private companiesService:CompaniesService,
    private notifier: NotifierService,
    private spinner: NgxSpinnerService,
    private jobService:JobsService,
    private router:Router,
  ) { }

  ngOnInit() {
    this.current_user = JSON.parse(localStorage.getItem('user'));
    this.route.params.subscribe(
      (params:Params)=>{
          this.companySlug=params.compSlug;
          this.jobSlug=params.slug;
          this.getShortListed();

      }
    )
  }
  getShortListed(){
    this.jobService.getShortListed(this.companySlug,this.jobSlug).subscribe(
      (res:any)=>{
        this.pickedUps=res.results;
        this.Next=res.next;
        }
    )
  }

}
