import { Component, OnInit, ViewChild, ElementRef, HostListener } from '@angular/core';
import { JobsService, CompaniesService } from 'src/app/services';
import { ActivatedRoute, Params } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Job } from 'src/app/models';
import { ProfileService } from 'src/app/providers/profile/profile.service';
import { NotifierService } from 'angular-notifier';
@Component({
  selector: 'app-job-interview-schedule',
  templateUrl: './job-interview-schedule.component.html',
  styleUrls: ['./job-interview-schedule.component.css']
})
export class JobInterviewScheduleComponent implements OnInit {
  current_user
  companyslug
  job_slug
  company
  job:Job={
    title:"",
    description:"",
    have_daily_perks: false,
    daily_perks_budget:null,
    have_transportation: false,
    transportation_budget:null,
    have_meal:false,
    meal_budget:null,
    have_space_rest: false,
    space_rest_budget: null,
    is_male: false,
    is_female: false,
    age:null,
    hide_company: false,
    latitude: null,
    longitude: null,
    category:[],
    publish_date:"",
    job_category:[]
  }
  originalJob;
  companyName;
  is_company;
  lookUps
  users;
  Next=null
  once=false;

  selectedUser:any;
  showTrue=false
  datesOfInterview=[]
  days = new Array( "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday" )
  dates=[]
  @HostListener('window:scroll', ['$event'])
  scroll() {
    if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight) {
      // you're at the bottom of the page
      if(this.Next&&!this.once){
        this.once=true;
          this.jobService.getNext(this.Next).subscribe(
            (res:any)=>{
              this.Next=res.next
              this.once=false
              this.users=[...this.users,...res.results];
              for(let i=0;i<this.users.length;i++)
              {
                this.users[i]['day']=this.days[new Date(this.users[i].interview_date).getDay()-1];
                let date=new Date(this.users[i].interview_date).toDateString();
                // date=date.substr(date.indexOf(' ')+1);
                this.users[i]['date']=date;
                let hours=new Date(this.users[i].interview_date).getHours();
                let minutes=new Date(this.users[i].interview_date).getMinutes();
                this.users[i]['time']=(hours<10? ('0'+hours):hours)+':'+(minutes<10? ('0'+minutes):minutes);
                this.dates[i]=date;
              }

              this.datesOfInterview=this.removeDuplicateUsingFilter(this.dates);
            }
          )
        }
    }

    }



  constructor(
    private jobService:JobsService,
    private route: ActivatedRoute,
    private companiesService:CompaniesService,
    private spinner:NgxSpinnerService,
    private profile: ProfileService,
    private notifier: NotifierService,
  ) { }
  ngOnInit() {

    this.current_user = JSON.parse(localStorage.getItem('user'));

    this.route.params.subscribe(
      (params:Params)=>{
          console.log(params)
          this.job_slug=params.slug;
          console.log(params.compSlug)
          this.companyslug=params.compSlug;
          this.spinner.show();
          this.companiesService.getCompany(this.current_user.slug,this.companyslug).subscribe(
            res=>{
              this.company=res;

              this.companyName=res.name;
              if(localStorage.getItem('switch'))
                {
                  this.is_company=true;
                  this.selectedForInterview();

                }

            }
          )
      }
    )
  }
  selectedForInterview(){
  this.jobService.selectedForInterview(this.companyslug,this.job_slug).subscribe(
    (res:any)=>{

      this.users=res.results;
      for(let i=0;i<this.users.length;i++)
      {
        this.users[i]['day']=this.days[new Date(this.users[i].interview_date).getDay()-1];
        let date=new Date(this.users[i].interview_date).toDateString();
        // date=date.substr(date.indexOf(' ')+1);
        this.users[i]['date']=date;
        let hours=new Date(this.users[i].interview_date).getHours();
        let minutes=new Date(this.users[i].interview_date).getMinutes();
        this.users[i]['time']=(hours<10? ('0'+hours):hours)+':'+(minutes<10? ('0'+minutes):minutes);
        this.dates[i]=date;
      }

      this.datesOfInterview=this.removeDuplicateUsingFilter(this.dates);
      this.Next=res.next;
      this.spinner.hide();
    },err=>{
      this.spinner.hide();
    }
  );
}
removeDuplicateUsingFilter(arr){
  let unique_array = Array.from(new Set(arr))
    return unique_array
}
}

