import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JobsComponent } from './jobs.component';
import { CreateJobComponent } from './create-job/create-job.component';
import { JobPageComponent } from './job-page/job-page.component';
import { JobDashboardComponent } from './job-dashboard/job-dashboard.component';
import { JobApplicantsComponent } from './job-applicants/job-applicants.component';
import { JobPickedUpComponent } from './job-picked-up/job-picked-up.component';
import { JobInterviewScheduleComponent } from './job-interview-schedule/job-interview-schedule.component';

const routes: Routes = [
  {
    path:'',
    component:JobsComponent,
    children:[
      {
        path: '',
        redirectTo: 'create-job/:slug',
        pathMatch: 'full'
      },{
        path:'create-job/:slug',
        component:CreateJobComponent
      },
      // {
      //   path:'job-page/:slug',
      //   component:JobPageComponent,
      // },
      {
        path:'job-page/:slug/:compSlug',
        component:JobPageComponent,
      },{
        path:'job-dashboard/:slug/:compSlug',
        component:JobDashboardComponent,
      }
      // ,{
      //   path:'job-applicants/:slug/:compSlug',
      //   component:JobApplicantsComponent,
      // },{
      //   path:'job-picked-up/:slug/:compSlug',
      //   component:JobPickedUpComponent,
      // },{
      //   path:'job-interview-schedule/:slug/:compSlug',
      //   component:JobInterviewScheduleComponent,
      // }
      ,{
        path: '**',
        redirectTo: 'create-job', 
        pathMatch: 'full'
    }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class JobRoutingModule { }
