import { Component, OnInit, ViewChild } from '@angular/core';
import { JobsService, CompaniesService } from 'src/app/services';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Job } from 'src/app/models';
import { CheckLocalStorageService } from 'src/app/services/api/checkLocalStorage.service';
import { NotifierService } from 'angular-notifier';
import {Options} from 'ng5-slider';

@Component({
  selector: 'app-job-page',
  templateUrl: './job-page.component.html',
  styleUrls: ['./job-page.component.css']
})
export class JobPageComponent implements OnInit {
  current_user
  companyslug
  job_slug
  company
  job:Job={
    title:"",
    description:"",
    have_daily_perks: false,
    daily_perks_budget:null,
    have_transportation: false,
    transportation_budget:null,
    have_meal:false,
    meal_budget:null,
    have_space_rest: false,
    space_rest_budget: null,
    is_male: false,
    is_female: false,
    age:null,
    hide_company: false,
    latitude: null,
    longitude: null,
    category:[],
    publish_date:"",
    job_category:[]
  }
  originalJob;
  companyName;
  is_company;
  lookUps
  editTitle=false;
  editDescription=false;
  editTags=false;
  is_applied=false;
  @ViewChild('form') form;
  high = 60

  options: Options = {
    floor: 0,
    ceil: 120
  };

  constructor(
    private jobService:JobsService,
    private route: ActivatedRoute,
    private companiesService:CompaniesService,
    private spinner:NgxSpinnerService,
    private checkLocalStorage:CheckLocalStorageService,
    private router:Router,
    private notifier: NotifierService,
  ) { }

  ngOnInit() {
    this.current_user = JSON.parse(localStorage.getItem('user'));
    this.getLookUps();
    this.route.params.subscribe(
      (params:Params)=>{
          this.job_slug=params.slug;

          this.companyslug=params.compSlug;
          this.spinner.show();
          this.companiesService.getCompany(this.current_user.slug,this.companyslug).subscribe(
            res=>{
              this.company=res;

              this.companyName=res.name;


              if(localStorage.getItem('switch'))
                {
                  this.is_company=true;
                  this.getJobForAdmin();
                }
              else if(!localStorage.getItem('switch'))
                {
                  this.is_company=false;
                  this.getJobForUser();
                }


            }
          )
      }
    )
  }

  getJobForAdmin(){
        this.jobService.getJobBySlugForAdmin(this.companyslug,this.job_slug).subscribe(
          (res:any)=>{

            this.job=res;
            this.job.category=[];

            for(let i=0;i<this.job.job_category.length;i++)
            {
              this.job.job_category[i].job_categories['visible']=true;
            }
            this.originalJob=Object.assign({},this.job);
            this.spinner.hide();


          }
        )
  }
  getJobForUser(){
    this.jobService.getJobBySlugForUser(this.companyslug,this.job_slug).subscribe(
      (res:any)=>{

        this.job=res;
        this.job.category=[];

        for(let i=0;i<this.job.job_category.length;i++)
        {
          this.job.job_category[i].job_categories['visible']=true;
        }
        this.originalJob=Object.assign({},this.job);
        this.spinner.hide();

      }
    )
}
 updateJob(tag?){
    // if(tag)
    // {
    //   this.removeTag(tag);
    // }
    this.job.category=[];
    for(let i=0;i<this.job.job_category.length;i++){
      this.job.category[i]=this.job.job_category[i].job_categories.id;
    }
    this.spinner.show();
    this.jobService.updateJob(this.companyslug,this.job_slug,this.job).subscribe(
      (res:any)=>{
        this.job=res;
        for(let i=0;i<this.job.job_category.length;i++)
            {
              this.job.job_category[i].job_categories['visible']=true;
            }
        this.spinner.hide();
        this.originalJob=Object.assign({},this.job);
      }
    )
  }
  resetJob(){
    this.job=Object.assign({},this.originalJob);
  }


 applyForJob(){

   this.jobService.applyForJob(this.companyslug,this.job_slug).subscribe(
     res=>{

       this.is_applied=true;
       this.notifier.notify('success','Congrats!, You have been Applied for this job');
     },err=>{
      this.notifier.notify('error','Some thing wrong happened');
     }
   )
 }
 getLookUps(){
  this.companiesService.getLookUps().subscribe(
    (res:any)=>{

      this.lookUps=res;
      this.addLookUpBoolean();
    }
  )
}
addLookUpBoolean(){
  for(let i=0;i<this.lookUps.length;i++)
  {
    this.lookUps[i]['visible']=false;
  }
}
pushTagId(lookup){
    let flag=this.job.job_category.filter(function(ele){
    return ele.job_categories.id == lookup.id;});
    console.log(flag.length);

if(!flag.length)
  {
    let job_categories={
      'job_categories':lookup
    }
    this.job.job_category.push(job_categories);
  }

}

removeTagId(id) {
  this.job.job_category=this.job.job_category.filter(function(ele){
          console.log("ele.job_categories  ",ele.job_categories);
            return ele.job_categories.id != id;
        });

}
addRemoveLookUp(lookup){
if(lookup['visible'])
{
  this.pushTagId(lookup);
}
else
  this.removeTagId(lookup.id);
}
}
