import { Component, OnInit, ViewChild } from '@angular/core';
import { Options } from 'ng5-slider';
import { JobsService, CompaniesService } from 'src/app/services';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Job } from 'src/app/models';

@Component({
  selector: 'app-create-job',
  templateUrl: './create-job.component.html',
  styleUrls: ['./create-job.component.css']
})
export class CreateJobComponent implements OnInit {
  options: Options = {
    floor: 0,
    ceil: 100
  };
  valueLow;
  high
  current_user
  compny_slug
  job:Job={
    title:"",
    description:"",
    have_daily_perks: false,
    daily_perks_budget:0,
    have_transportation: false,
    transportation_budget:0,
    have_meal:false,
    meal_budget:0,
    have_space_rest: false,
    space_rest_budget: 0,
    is_male: false,
    is_female: false,
    age:null,
    hide_company: false,
    latitude: null,
    longitude: null,
    category:[],
  }
  category
  lookUps;
  @ViewChild('form') form;
  constructor(
    private jobsService:JobsService,
    private route:ActivatedRoute,
    private companiesService:CompaniesService,
    private router:Router
  ) { }

  ngOnInit() {
   this.valueLow=18;
   this.high=60;
   this.current_user = JSON.parse(localStorage.getItem('user'));
    console.log(this.current_user.slug)
    this.route.params.subscribe(
      (params:Params)=>{
          this.compny_slug=params.slug;
          // this.getJobs();

      })
      this.getLookUps() ;
  }

  getLookUps(){
    this.companiesService.getLookUps().subscribe(
      (res:any)=>{
        this.lookUps=res;
        this.addLookUpBoolean();
      }
    )
  }
  addLookUpBoolean(){
    for(let i=0;i<this.lookUps.length;i++)
    {
      this.lookUps[i]['visible']=false;
    }
  }
  pushTagId(id){
    if(this.job.category.length==0)
      this.job.category.push(id);
    else if(!this.job.category.includes(id))
      this.job.category.push(id);
  }

  removeTagId(id) {

    this.job.category=this.job.category.filter(function(ele){
        return ele != id;
    });
 }
 addRemoveLookUp(lookup){
  if(lookup['visible'])
  {
    this.pushTagId(lookup.id);
  }
  else
    this.removeTagId(lookup.id);
 }
  change(event){
  }
  getJobs()
  {
    this.jobsService.get(this.compny_slug).subscribe(
      (res:any)=>{
      }
    )
  }
  postJob(){

    this.jobsService.post(this.compny_slug,this.job).subscribe(
      (res:any)=>{
        this.router.navigate(['company/company-page/',this.compny_slug]);
      }
    )
  }


}
